import request from '@/utils/request'
// get



// post

// 
export function getFinanceApplicationList(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/GetFinanceApplicationList',
        method: 'post',
        data
    })
}

// 
export function getSysGroupList(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/GetSysGroupList',
        method: 'post',
        data
    })
}

// 
export function saveFinanceApplication(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/SaveFinanceApplication',
        method: 'post',
        data
    })
}

// 
export function getFinanceApplicationInfoByPost(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/GetFinanceApplicationInfoByPost',
        method: 'post',
        data
    })
}

// 
export function shenHeFinanceApplication(data) {
    return request({
        url: '/Api/V1/SysGroupAccount/ShenHeFinanceApplication',
        method: 'post',
        data
    })
}


