<template>
  <div>

    <el-breadcrumb style="height:30px;" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/merchantsMoney' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 130px"
      class="box-card"
    >
      <el-row style="margin-bottom: 10px">

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder=""
            ></el-input>
          </div>
        </el-col>


        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder=""
            ></el-input>
          </div>
        </el-col>

        

        
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-select
              style="width: 200px"
              v-model="search.type"
              placeholder=""
              clearable
            >
              <el-option
                v-for="item in type"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-select
              style="width: 200px"
              v-model="search.label"
              placeholder=""
              clearable
            >
              <el-option
                v-for="item in label"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="3">
          <el-button @click="searchEvent" type="primary"></el-button>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>

      <el-row>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.number"
              placeholder=""
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-select
              style="width: 200px"
              v-model="search.level"
              placeholder=""
            >
              <el-option
                v-for="item in level"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>


        <el-col :span="6">
          <!-- <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-date-picker
              v-model="search.time"
              style="width: 240px"
              type="daterange"
              start-placeholder=""
              end-placeholder=""
            >
            </el-date-picker>
          </div> -->

          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker
              v-model="search.time"
              type="datetimerange"
              range-separator=""
              style="width: 240px"
              start-placeholder=""
              value-format="yyyy-MM-dd HH:mm"
              end-placeholder="">
            </el-date-picker>
          </div>
        </el-col>

        
      </el-row>
    </el-card>
    <!--  -->
    <el-card shadow="never" class="card_bottom box-card">
      <!--  -->
      <el-table
        height="calc(100vh - 350px)"
        highlight-current-row
        :data="tableData"
        v-loading="tableLoading"
      >
        <el-table-column
          fixed
          prop="transBatch"
          label=""
          width="180"
        >
        </el-table-column>
        <el-table-column
          prop="transType"
          label=""
          width="130"
        >
        </el-table-column>
        <el-table-column
          prop="tag"
          label=""
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="transMoney"
          label=""
          width="130"
        >
        </el-table-column>
        <el-table-column
          prop="newMoney"
          label=""
          width="130"
        >
        </el-table-column>
        <el-table-column
          prop="addTimeString"
          label=""
          width="170"
        >
        </el-table-column>
        <el-table-column
          prop="sourceOrderNo"
          label="\"
          width="240"
        >
        </el-table-column>
        <el-table-column
          prop="groupName"
          label=""
          width="180"
        >
        </el-table-column>
        <el-table-column
          prop="groupLevel"
          label=""
          min-width="130"
        >
        </el-table-column>
        
      </el-table>
      <!--  -->
      <div class="pading">
        <el-pagination @current-change="pagingEvent" background layout="prev, pager, next" :total="paging.pages">
        </el-pagination>
      </div>
    </el-card>

  </div>
</template>

<script>
import {queryCzTrans} from '@/api/merchantsMoney.js'
export default {
  data() {
    return {
      search: {
        phone: "",
        name: "",
        level: 0,
        time: "",
        number:"",
        type:0,
        label:0
      },
      label:[
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
        {
          value: 2,
          label: "",
        },
        {
          value: 3,
          label: "",
        },
        {
          value: 4,
          label: "-",
        },
        {
          value: 5,
          label: "-",
        },
        {
          value: 6,
          label: "-",
        },
        {
          value: 7,
          label: "",
        },
        {
          value: 8,
          label: "",
        },
        {
          value: 9,
          label: "",
        },
      ],
      level: [
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
        {
          value: 2,
          label: "",
        },
        {
          value: 3,
          label: "",
        },
      ],
      type: [
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
        {
          value: 2,
          label: "",
        },
      ],
      tableLoading:false,
      paging:{},
      tableData: [],
    };
  },
  async created() {
    console.log('start');
    console.log(this.$route.params.id);
    this.tableLoading = true
    var n1 = await queryCzTrans({
      groupID: "1000",
      groupLevel: this.search.level,
      tradeType: this.search.type,
      beginTime: this.search.time[0],
      endTime: this.search.time[1],
      accountType: 1,
      groupName: this.search.name,
      groupPhone: this.search.phone,
      tradeNo: this.search.number,
      pageNumber: 1,
      pageSize: 10,
    })
    this.tableData = n1.data.data
    this.paging = n1.data.pagination
    this.tableLoading = false
  },
  methods:{
    // 
    pagingEvent(e){
      // e
      console.log(e);
    },
    // 
    async searchEvent(){
      console.log(this.search);
      this.tableLoading = true
      var n1 = await queryCzTrans({
        groupID: "1000",
        groupLevel: this.search.level,
        tradeType: this.search.type,
        beginTime: this.search.time[0],
        endTime: this.search.time[1],
        accountType: 1,
        groupName: this.search.name,
        groupPhone: this.search.phone,
        tradeNo: this.search.number,
        pageNumber: 1,
        pageSize: 10,
      })
      this.tableData = n1.data.data
      this.paging = n1.data.pagination
      this.tableLoading = false
    }
  }
};
</script>

<style>
.card_bottom {
  width: 100%;
  height: calc(100vh - 260px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
</style>


