<template>
  <div>
    <el-breadcrumb style="height: 30px" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card
      shadow="never"
      style="margin-bottom: 6px; height: 80px"
      class="box-card"
    >
      <el-row>
        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.phone"
              placeholder=""
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.name"
              placeholder=""
            ></el-input>
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-input
              style="width: 200px"
              maxlength="11"
              v-model="search.city"
              placeholder=""
            ></el-input>
            <!-- <el-cascader :clearable="true" :options="city">
              <template slot-scope="{ node, data }">
                <span>{{ data.label }}</span>
                <span v-if="!node.isLeaf"> ({{ data.children.length }}) </span>
              </template>
            </el-cascader> -->
          </div>
        </el-col>

        <el-col :span="5">
          <div style="color: #333; font-size: 15px" class="demo-input-suffix">
            
            <el-select
              style="width: 200px"
              v-model="search.state"
              placeholder=""
            >
              <el-option
                v-for="item in state"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="4">
          <el-button @click="searchEvent" type="primary"></el-button>
          <el-button @click="apply" type="danger"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-card shadow="never" class="card_bottom box-card">
      <el-table
        highlight-current-row
        :data="tableData"
        height="calc(100vh - 300px)"
        v-loading="tableLoading"
      >
        <el-table-column fixed prop="groupName" label="" width="170">
        </el-table-column>
        <el-table-column prop="levelCN" label="" width="120">
        </el-table-column>
        <el-table-column prop="telephone" label="" width="170">
        </el-table-column>
        <el-table-column prop="proviceCity" label="" width="150">
        </el-table-column>
        <el-table-column prop="address" label="" width="240">
        </el-table-column>
        <el-table-column prop="applicationStatusCN" label="" width="90">
        </el-table-column>
        <el-table-column prop="applicationTimeCN" label="" width="210">
        </el-table-column>
        <el-table-column prop="trueName" label="" width="140">
        </el-table-column>
        <el-table-column label="" min-width="120">
          <template>
            <el-button type="text" size="small"></el-button>
            <el-button type="text" size="small"></el-button>
            <el-button type="text" size="small"></el-button>
            <el-button type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-dialog title="" width="36%" :visible.sync="dialogFormVisible">
        <el-form style="padding-left: 70px" :model="form">
          <el-form-item
            style="width: 80%"
            label="ID"
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.merchantsID" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item
            style="width: 80%"
            label="ID"
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.userID" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item
            style="width: 80%"
            label="ID"
            :label-width="formLabelWidth"
          >
            <el-input v-model="form.merchantID" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item
            style="width: 80%"
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              type="textarea"
              placeholder=""
              v-model="form.note"
              maxlength="30"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="auditOpen"></el-button>
          <el-button type="primary" @click="dialogFormVisible = false"
            ></el-button
          >
        </div>
      </el-dialog>

      <!--  -->
      <div class="pading">
        <el-pagination background layout="prev, pager, next" :total="paging.pages">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { provinces } from "@/utils/city.json";
import {
  getFinanceApplicationList,
  shenHeFinanceApplication,
} from "@/api/openFinancial.js";
export default {
  data() {
    return {
      form: {
        merchantsID: "",
        userID: "",
        merchantID: "",
        note: "",
      },
      formLabelWidth: "125px",
      dialogFormVisible: false,

      search: {
        phone: "",
        name: "",
        city: "",
        state: null,
      },
      city: provinces,
      state: [
        {
          value: null,
          label: "",
        },
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
        {
          value: 2,
          label: "",
        },
        {
          value: 3,
          label: "",
        },
      ],
      tableLoading: false,
      paging: {},
      tableData: [],
    };
  },
  methods: {
    apply() {
      this.$router.push("/applyOpen");
    },
    // 
    async searchEvent() {
      this.tableLoading = true;
      var n1 = await getFinanceApplicationList({
        groupName: this.search.name,
        city: this.search.city,
        telephone: this.search.phone,
        level: this.search.state,
        pageNumber: 1,
        pageSize: 10,
      });
      this.tableData = n1.data.data;
      this.paging = n1.data.pagination;
      this.tableLoading = false;
    },
    // 
    async auditOpen() {
      var n1 = await shenHeFinanceApplication({
        groupName: this.form.merchantsID,
        city: this.form.userID,
        telephone: this.form.merchantID,
        level: this.form.note,
      });
      console.log(n1);
    },
  },
  async created() {
    // 
    this.tableLoading = true;
    var n1 = await getFinanceApplicationList({
      groupName: "",
      city: "",
      telephone: "",
      level: null,
      pageNumber: 1,
      pageSize: 10
    });

    // var n1 = await getFinanceApplicationList({
    //   groupID: "1000",
    //   groupLevel: 0,
    //   tradeType: 0,
    //   beginTime: "",
    //   endTime: "",
    //   accountType: 1,
    //   groupName: "",
    //   groupPhone: "",
    //   tradeNo: "",
    //   pageNumber: 1,
    //   pageSize: 10,
    // });
    this.tableData = n1.data.data;
    this.paging = n1.data.pagination;
    this.tableLoading = false;
  },
};
</script>
<style scoped>
.card_bottom {
  width: 100%;
  height: calc(100vh - 210px);
  position: relative;
}
.pading {
  position: absolute;
  width: 200px;
  right: 240px;
  bottom: 20px;
}
</style>



