<template>
    <div class="loginbj" :class="{setloginbj: windowWidth<1366}">
        <!--  -->
        <div id="mydiv">
            <!-- canvas -->
        </div>
        <div class="loginForm">
            <h2 class="logintitle"></h2>
            <el-form :model="login" :rules="rules" ref="ruleForm" label-width="66px" class="demo-ruleForm">
                <el-form-item label="" prop="username">
                    <el-input v-model="login.username"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="login.password" type="password"></el-input>
                </el-form-item>
                <el-form-item label="" class="setitem">
                    <el-button type="primary" class="loginbtn" @click="goLogin('ruleForm')"> </el-button>
                </el-form-item>
            </el-form>
        </div>
        <!-- <el-button type="primary" @click="goLogin"></el-button> -->
    </div>
</template>

<script>
import { getUserInfo } from '@/api/login.js'
export default {
    data() {
        return {
            login: {
                username: '',
                password: ''
            },
            windowWidth: 1600,
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                ]
            }
        };
    },
    mounted (){
        //
        let config = {
            domId: 'mydiv', //dom
            vx: 2, //x,
            vy: 2, //y
            height: 2, //
            width: 2,
            count: 150, //
            color: "255,255,255", //
            stroke: "255,255,255", //
            dist: 6000, //
            //dist: 6000, //
            e_dist: 4000, //
            //e_dist: 40000, //
            max_conn: 10 //
            //max_conn: 40 //
            //, ""
        }
        //
        CanvasParticle(config);
        let svg = document.getElementsByTagName('svg');
        //console.log(svg);
        for (let i = 0; i < svg.length; i++) {
            svg[i].setAttribute('focusable', false);
        }
    },
    created() {
        this.windowWidth = document.body.clientWidth;
    },
    methods: {
        goLogin(formName) {
            this.$router.push({ path: "/home", query: {} });
            // this.$refs[formName].validate(async(valid) => {
            //     if (valid) {
            //         const info = await getUserInfo();
            //         if(info.success) {
            //             window.sessionStorage.setItem('userItem', JSON.stringify(info.data));
            //             this.$router.push({ path: "/home", query: {} });
            //         } else {
            //             this.$alert('', '', {
            //                 confirmButtonText: ''
            //             });
            //         }
            //     } else {
            //         return false;
            //     }
            // });
        },
    },
};
</script>

