<template>
  <div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-card style="margin-bottom: 20px" class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
          <el-button style="float: right; padding: 3px 0" type="text"
            ></el-button
          >
        </div>
        <el-row>
          <el-col :span="12">
            <el-form class="oneform" label-width="120px">
              <el-form-item label="">
                <span>xxx</span>
              </el-form-item>
              <el-form-item label="">
                <span style="color: red">200.00</span>
              </el-form-item>
              <el-form-item label="">
                <span>2020-05-20 122334</span>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="">
                <span></span>
              </el-form-item>
              <el-form-item label="">
                <span>62232267656655232</span>
              </el-form-item>
              <el-form-item label="">
                <span></span>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-card>

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <el-row>
          <el-col :span="12">
            <el-form label-width="120px">
              <el-form-item label="">
                <el-select v-model="value" placeholder="">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input
                  v-model="input"
                  placeholder="200.00"
                ></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="value" placeholder="">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input
                  type="textarea"
                  placeholder=""
                  v-model="textarea"
                  maxlength="30"
                  show-word-limit
                >
                </el-input>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12"> </el-col>
        </el-row>
      </el-card>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Bus from '@/utils/bus.js'
export default {
  data() {
    return {
      dialogFormVisible: false,
      textarea: "",
      input: "",
      options: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
        {
          value: "4",
          label: "",
        },
        {
          value: "5",
          label: "",
        },
      ],
    };
  },
  created(){
      Bus.$on('chuli',()=>{
          this.dialogFormVisible = true
      })
  }
};
</script>

<style scoped>
.oneform .el-form-item {
  margin-bottom: 0 !important;
}
.dialog-footer {
  text-align: center !important;
}
.el-button {
  width: 140px;
}
.el-dialog__header {
  background-color: #5da2de;
}
</style>


